* {
  font-size: 100%;
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter), sans-serif;
  color: var(--White_Text);
  background-color: var(--VeryDarkBlue_BG);
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

:root {
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram_start: hsl(37, 97%, 70%);
  --Instagram_end: hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --Toggle_dark_start: hsl(210, 78%, 56%);
  --Toggle_dark_end: hsl(146, 68%, 55%);
  --Toggle_light: hsl(230, 22%, 74%);
  --VeryDarkBlue_BG: hsl(230, 17%, 14%);
  --VeryDarkBlue_TopBGPattern: hsl(232, 19%, 15%);
  --DarkDesaturatedBlue_CardBG: hsl(228, 28%, 20%);
  --DesaturatedBlue_Text: hsl(228, 34%, 66%);
  --White_Text: hsl(0, 0%, 100%);
  --White_BG: hsl(0, 0%, 100%);
  --VeryPaleBlue_TopBGPattern: hsl(225, 100%, 98%);
  --LightGrayishBlue_CardBG: hsl(227, 47%, 96%);
  --DarkGrayishBlue_Text: hsl(228, 12%, 44%);
  --VeryDarkBlue_Text: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter";
}

.container {
  max-width: 69.375rem;
  padding-left: 1.4375rem;
  padding-right: 1.4375rem;
  margin: 0 auto;
}
@media (min-width: 47.5em) {
  .container {
    padding-left: 10.125rem;
    padding-right: 10.125rem;
  }
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 1.3125rem;
  margin-bottom: 0.8125rem;
}
@media (min-width: 47.5em) {
  h1 {
    font-size: 1.1875rem;
  }
}

p {
  margin: 0;
  font-size: 0.75rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--White_Text);
}

.header {
  padding-top: 2.5625rem;
}
@media (min-width: 47.5em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  border-bottom: solid;
  border-width: 0.0625rem;
  border-color: var(--DarkGrayishBlue_Text);
  padding-bottom: 1.375rem;
}
@media (min-width: 47.5em) {
  .header__title {
    border: none;
    padding-bottom: 0;
  }
}
.header__title p {
  color: var(--DesaturatedBlue_Text);
}

fieldset {
  border: none;
  margin-top: 1.375rem;
  padding: 0;
}
@media (min-width: 47.5em) {
  fieldset {
    margin: 0;
  }
}

.toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 47.5em) {
  .toggle {
    display: block;
  }
}
.toggle label {
  font-size: 0.75rem;
  margin-right: 0.875rem;
  color: var(--DesaturatedBlue_Text);
}
@media (min-width: 47.5em) {
  .toggle label {
    color: var(--White_Text);
  }
}
.toggle__wrapper {
  display: inline-block;
  position: relative;
  height: 1.5rem;
  width: 3rem;
  cursor: pointer;
  vertical-align: middle;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(225deg, var(--Toggle_dark_start) 0%, var(--Toggle_dark_end) 98.02%);
  border-radius: 0.75rem;
  overflow: hidden;
  z-index: 0;
}
.toggle input[type=radio] {
  display: inline-block;
  height: 18px;
  margin: 0 3px 0px 0;
  opacity: 0;
  width: 18px;
  z-index: 1;
  position: relative;
  top: 3px;
  left: 3px;
}
.toggle__switcher {
  display: block;
  position: absolute;
  height: 1.125rem;
  width: 1.125rem;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  border-radius: 50%;
  background-color: var(--DarkDesaturatedBlue_CardBG);
  z-index: 2;
  transition: all 150ms ease-out;
}

#toggle__light:checked ~ .toggle__switcher {
  right: 0;
  left: 50%;
}